<template>
  <div id="memberLogin">
    <!-- 标题部分 -->
    <van-row class="logo">
      <van-col span="4" offset="10">
        <img src="../../assets/img/menu1.png" />
      </van-col>
    </van-row>
    <van-row>
      <van-col span="6" offset="9" class="title">
        <p>账号登录</p>
      </van-col>
    </van-row>
    <!-- 登录表单 -->
    <!-- 判断是通过用户名登录还是手机号登录或注册 -->
    <template v-if="currentKey==='username'">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="用户名"
          placeholder="邮箱/手机号码/用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
          :key="currentKey"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
          :key="currentKey+1"
        />
        <div style="margin: 16px;">
          <van-button block type="info" native-type="submit" class="subbtn">登录</van-button>
        </div>
      </van-form>
      <van-row>
        <van-col span="22" offset="1" class="title">
          <van-button
            plain
            type="default"
            style="width: 100%"
            @click="changeKey('mobile')"
          >手机短信登录/注册</van-button>
        </van-col>
      </van-row>
      <van-row>
        <van-col span="22" offset="1" class="registerBar">
          <p>
            <router-link to class="register">立即注册</router-link>|
            <router-link to class="register">忘记密码</router-link>
          </p>
        </van-col>
      </van-row>
    </template>
    <!-- 如果当前key为moboile -->
    <template v-else-if="currentKey==='mobile'">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          type="number"
          name="username"
          label="+86>"
          placeholder="手机号码"
          :rules="[{ required: true, message: '请填写手机号码' }]"
          :key="currentKey"
        />
        <van-field
          v-model="password"
          center
          clearable
          label
          placeholder="请输入短信验证码"
          :key="currentKey+1"
        >
          <!-- 如果是通过判断模板显示的结构中还有嵌套的模板，则需要将条件再次判断 -->
          <template #button v-if="currentKey==='mobile'">
            <van-button
              size="small"
              type="primary"
              style="background: #fff;color: #2EA5E5;border: none"
            >发送验证码</van-button>
          </template>
        </van-field>
        <div style="margin: 16px;">
          <van-button block type="info" native-type="submit" class="subbtn">立即登录/注册</van-button>
        </div>
      </van-form>
      <van-row>
        <van-col span="22" offset="1" class="title">
          <van-button
            plain
            type="default"
            style="width: 100%"
            @click="changeKey('username')"
          >用户名密码登录</van-button>
        </van-col>
      </van-row>
      <van-row>
        <van-col span="22" offset="1" class="registerBar">
          <p>
            <router-link to class="register">收不到验证码？</router-link>
          </p>
        </van-col>
      </van-row>
    </template>
    <van-divider :style="{ color: '#847878', borderColor: '#9C9393', padding: '0 16px' }">其他方式登录</van-divider>
    <van-row>
      <van-col span="8" offset="8" class="other_methods">
        <ul>
          <li>
            <img src="../../assets/img/weibo.png" alt />
          </li>
          <li>
            <img src="../../assets/img/zhifubao.png" alt />
          </li>
          <li>
            <img src="../../assets/img/weixin.png" alt />
          </li>
        </ul>
      </van-col>
    </van-row>
  </div>
</template>

<script>
// import { Form } from 'vant'
export default {
  data () {
    return {
      username: '',
      password: '',
      // 设置当前状态，判断是通过用户名还是手机号登录注册
      currentKey: 'username'
    }
  },
  methods: {
    onSubmit () {
      if (this.username === 'admin' && this.password === '123456') {
        window.sessionStorage.setItem('token', Math.random())
        this.$toast.success('登录成功!')
        return this.$router.push('/member')
      }
      return this.$toast.fail('账号或密码错误!')
    },
    // 登录页面切换
    changeKey (newKey) {
      this.username = ''
      this.password = ''
      this.currentKey = newKey
    }
  }
}
</script>

<style lang="less" scoped>
#memberLogin {
  padding: 0.0625rem;
}
.logo {
  margin-top: 1rem;
  img {
    width: 100% !important;
  }
}
.title {
  font-size: 20px;
  text-align: center;
}
.subbtn {
  border: none;
  border-radius: 0.125rem;
  background: #ff6700;
  font-size: 18px;
}
.registerBar {
  text-align: center;
  margin-top: 10px;
  .register {
    margin: 0 10px;
    font-size: 18px;
    color: #a4a4a4;
  }
}
.other_methods {
  ul {
    display: flex;
    justify-content: space-around;
  }
}
</style>
